          <button
            type="submit"
            class="login-button"
            :class="{ 'loading': isLoading }"
            :disabled="isLoading"
          >
            <span v-if="!isLoading" class="button-content">
              <i class="fas fa-sign-in-alt"></i>
              Log in
            </span>
            <span v-else class="button-content">
              <span class="spinner"></span>
